import React, { useState, useMemo, useCallback } from 'react';
import { Pagination, PaginationItem, PaginationLink } from 'reactstrap';
import { FontAwesomeIcon } from '@fortawesome/react-fontawesome';

export const BPagination = (props) => {
    const { dataCount, pageSize = 10, pageChange } = props;
    const [pageIndex, setPageIndex] = useState(0);
    const pageCount = Math.ceil(dataCount / pageSize);
    const pageIndexList = useMemo(() => pageCount > 0 ? [...new Array(pageCount)].fill(null).map((d, i) => i + 1) : [], [pageCount]);
    const canPreviousPage = pageIndex > 0;
    const canNextPage = pageCount === -1 ? dataCount >= pageSize : pageIndex < pageCount - 1;
    const gotoPage = useCallback(pageIndex => { setPageIndex(pageIndex); pageChange(pageIndex); }, [setPageIndex, pageChange]);
    const previousPage = useCallback(() => { gotoPage(pageIndex - 1) }, [gotoPage, pageIndex])
    const nextPage = useCallback(() => { gotoPage(pageIndex + 1) }, [gotoPage, pageIndex])

    return (
        <Pagination className="pagination-primary mb-2">
            <PaginationItem disabled={!canPreviousPage}>
                <PaginationLink
                    first
                    href="#/"
                    onClick={e => { e.preventDefault(); gotoPage(0); }}>
                    <FontAwesomeIcon icon={['fas', 'angle-double-left']} />
                </PaginationLink>
            </PaginationItem>
            <PaginationItem disabled={!canPreviousPage}>
                <PaginationLink
                    previous
                    href="#/"
                    onClick={e => { e.preventDefault(); previousPage(); }}>
                    <FontAwesomeIcon icon={['fas', 'chevron-left']} />
                </PaginationLink>
            </PaginationItem>
            {
                pageIndexList.map((item, index) => {
                    if (index >= pageIndex - 2 && index <= pageIndex + 2)
                        return (
                            <PaginationItem key={index} active={pageIndex === index}>
                                <PaginationLink key={index} href="#/" onClick={e => { e.preventDefault(); gotoPage(index); }}>
                                    {item}
                                </PaginationLink>
                            </PaginationItem>
                        )
                    else
                        return null;
                })
            }
            <PaginationItem disabled={!canNextPage}>
                <PaginationLink
                    next
                    href="#/"
                    onClick={e => { e.preventDefault(); nextPage(); }}>
                    <FontAwesomeIcon icon={['fas', 'chevron-right']} />
                </PaginationLink>
            </PaginationItem>
            <PaginationItem disabled={!canNextPage}>
                <PaginationLink
                    last
                    href="#/"
                    onClick={e => { e.preventDefault(); gotoPage(pageCount - 1); }}>
                    <FontAwesomeIcon icon={['fas', 'angle-double-right']} />
                </PaginationLink>
            </PaginationItem>
        </Pagination>
    )
}