import React, { useState } from 'react';
import { Col, FormGroup, Label, Input } from 'reactstrap';

import { BCard, BContainer, BTable } from '../../components';
import { parks } from '../../fakeData/parks';

const columns = [{ key: 'pm_name', title: '名稱', style: { width: '10%' } },
{ key: 'pm_name_eng', title: '英文名', style: { width: '10%' } },
{ key: 'pm_construction', title: '成立年度', style: { width: '10%' } },
{ key: 'pm_unit', title: '管理單位', style: { width: '20%' } },
{ key: 'pm_service', title: '設施', style: { width: '20%' } }];

export const Parks = () => {
  const [dataSource, setDataSource] = useState(parks.slice(0 * 10, (0 + 1) * 10));

  return (
    <BContainer fluid={true}>
      <BCard title='公園列表'>
        <FormGroup row>
          <Label htmlFor="pm_name" sm={2}>公園名稱</Label>
          <Col sm="4">
            <Input
              type="text"
              name="pm_name"
              id="pm_name"
              placeholder="公園名稱"
            />
          </Col>
          <Col sm="4">

          </Col>
        </FormGroup>
      </BCard>
      <BTable
        columns={columns}
        dataSource={dataSource}
        dataCount={parks.length}
        pageChange={(pageIndex) => { setDataSource(parks.slice(pageIndex * 10, (pageIndex + 1) * 10)); }}
      ></BTable>
    </BContainer>
  )
}