import React from 'react';
import { Table } from 'reactstrap';
import { BPagination } from './BPagination';

export const BTable = (props) => {
    const { columns, dataSource, dataCount, pageChange } = props;
    return (
        <div className="table-responsive-md">
            <BPagination dataCount={dataCount} pageChange={(pageIndex) => { pageChange(pageIndex) }}></BPagination>
            <Table hover bordered>
                <thead className="thead-light">
                    <tr>
                        {
                            columns.map((item, index) => {
                                return (<th scope="col" key={index} style={item.style}>{item.title}</th>)
                            })
                        }
                    </tr>
                </thead>
                <tbody>
                    {
                        dataSource.map((item, index) => {
                            return (
                                <tr key={index}>
                                    {
                                        columns.map((m, index) => {
                                            return (<td key={index}>{item[m.key]}</td>)
                                        })
                                    }
                                </tr>
                            )
                        })
                    }
                </tbody>
            </Table>
        </div>
    )
}