import React from 'react';
import {
    Card, CardBody, CardHeader, Button, CardFooter
} from 'reactstrap';

export const BCard = (props) => {
    const { title, children } = props;
    return (
        <Card className="mb-3">
            <CardHeader className="bg-night-sky" style={{ color: 'white' }}>{title}</CardHeader>
            <CardBody>
                {children}
            </CardBody>
            <CardFooter className="d-flex">
                <div className="mr-auto">
                    <Button outline color="danger">新增</Button>
                </div>
                <div className="ml-auto">
                    <Button outline color="primary" className="mr-2">查詢</Button>
                    <Button outline color="second">清除</Button>
                </div>
            </CardFooter>
        </Card>
    )
}