        "pm_overview": "　　北投地區早在距今6,500年前已有人類居住，目前已發現的新石器時代史前文化遺址共有15處，文化內涵豐富，從最早的大坌坑文化，到芝山岩文化、圓山文化、植物園文化，以及距今最晚近的鐵器時代十三行晚期文化，另外，也有可能為平埔族舊社的遺址。北投」總是給人說不完的人文、傳說及故事，下了新北投捷運站，跟著裊裊的溫泉煙霧前行，前方滿是綠意的溫泉鄉美景，就是北投公園。\r\n北投地區早在距今6,500年前已有人類居住，目前已發現的新石器時代史前文化遺址共有15處，文化內涵豐富，從最早的大坌坑文化，到芝山岩文化、圓山文化、植物園文化，以及距今最晚近的鐵器時代十三行晚期文化，另外，也有可能為平埔族舊社的遺址。北投」總是給人說不完的人文、傳說及故事，下了新北投捷運站，跟著裊裊的溫泉煙霧前行，前方滿是綠意的溫泉鄉美景，就是北投公園。 　　北投公園由北投1、2、4、54、55號等都市計畫公園所組成，面積達6萬1,129平方公尺。首先映入眼簾的，是98年度完工的入口跳泉，每日固定時間隨著音樂，有著燈光及水泉飛舞，為北投這個具有豐富文化的老台北地區，重新注入一股新生的活力，沿著光明路側，途經白千層及魯花樹等珍稀老樹，為已滿百歲的北投公園見證他豐富的歷史，旅途中也會經過由臺北市立圖書館首次建築的綠色圖書館，強調節能省碳，也為北投公園帶來書香氣息，門口的的噴水池更是著名電影場景之一哦！沿著北投溪前進，世界上唯一以臺灣地名命名的「北投石」，即出產於此，雖然現在已不復見，憑欄垂思猶發幽情。 　　越過小徑拾級而上，來到中山路側，除了北投露天溫泉浴池外，旁邊還有于右任先生提名之「梅庭」歷史建物，梅庭建於1930年代，是前監察院長于右任避暑、避壽的寓所，由於梅庭保存有豐富的日式木造建築，建物面積達250平方公尺，北市文化局在民國95年，將梅庭列為歷史建物目前已由本府觀光傳播局規劃為梅庭遊客中心，以提供遊客最貼心的服務。 　　還有聞名的溫泉博物館，日洋合一的木造窗櫺及紅磚建築體，似乎還瀰漫著濃濃硫磺的特有香味，感受日據時代建築之美感，北投溫泉浴場原建於1913年6月17日，佔地約700坪，是兩層樓仿英式磚造建築，為當時最大的公共溫泉浴場。1945年，國民政府接收台灣後，此浴場曾一度成為民眾服務社及游泳池，之後不再使用，任其荒廢。1995年，指定成了三級古蹟，當地居民開始組成推動小組，推動成為「居民親手打造自己社區的博物館」。積極地向臺北市政府申請，保留將這個公共溫泉浴場成為「北投溫泉博物館」。 　　北投溫泉是北台灣最具歷史情懷的溫泉泉鄉，早在1895年清朝政府將台灣割讓給日本，當時台灣的抗日義勇軍退入大屯山區，日本軍隊在圍剿過程中，陸續發現了北投一帶的溫泉地熱，從那時開始，就揭開了北投一百多年來的風花雪月的精彩歷史，不過在日本人發現溫泉之前，北投事實上是一座硫磺礦場，在1894年德國硫磺商人發現了北投溫泉並開設台灣第一家溫泉俱樂部，但隔年台灣進入日據時代，日本人來此探勘、分析，到了1896年當時有一位大阪人平田源吾來到北投，在泡過了北投溫泉之後大為驚豔，於是就在溪畔開辦了第一家、叫做「天狗庵」的溫泉旅社，迨至102年間闢建為天狗庵史蹟公園，面積769平方公尺，也正式開啟了北投溫泉史。也正式開啟了北投溫泉史。周邊潺潺溪流及綠蔭搖曳的景緻，置身其中，像是回到舊時的悠悠時光裡。 　　時至94年間完成北投公園更新，增設露天溫泉浴池，它不只是著名的溫泉區，那獨特的飲食文化、美食特色，更能代表北投過去半個世紀以來的演變與生命力時至今日，有些日本旅客到台灣旅遊時，仍然選擇要投宿在北投的溫泉會館或飯店，並尋找那「酒家菜」的古味。北投公園有說不完的老故事以及各式各樣動、植物群聚，周邊還有地熱谷等著名景點，不妨趁著假日，安排一趟洗滌身心的知性之旅，相信您一定滿載而歸。",
