        "pm_name": "榮華A公園",
        "pm_overview": "　　榮華公園坐落於北投區明德路150巷3弄附近，公園依次開闢於民國75、82年，面積約有1萬5千2百平方公尺，具有兒童遊具、體健設施、籃球、網球、羽球、溜冰場等設施，公園隱身於住宅區內，又具有完善的運動設施，是周邊重要的運動遊憩場所，公園內榕樹、第倫桃樹木茂密，提供晨運民眾與遊玩兒童良好的綠蔭，這裡有很多大樹是遮陽的好地方，尤其是夏天，公園內比周邊氣溫低了有2-3度，有很多地方可讓老人下棋、泡茶，裡面還有一座土地公廟，叫靈石福佑宮，香火鼎盛。\r\n重新整建後的榮華共融式遊戲場，使用少見的共融式雙滑軌滑道，其中具有安全帶的座椅式滑軌，可供各種不同年齡層及身體狀況的使用者遊玩，可供不耐久候的小小朋友們在滑軌外還有其他可以使用的設施。",
