        "pm_name": "東和公園",
        "pm_overview": "　　由中山北路七段114巷穿過天母東路86巷就可以到達東和公園，它的面積約有3.1公頃，於民國88年闢建完成，備有平面停車場、表演舞台，羽球場等設施，玉潮坑溪及其支流穿越其間，可說是純粹自然與休閒功能兼備的都會公園。每到假日就吸引許多全家大小來到公園遊憩，尤其在停車場上方斜坡草坪，享受滑草嬉戲或悠閒躺滾的天倫之樂。\r\n由中山北路七段114巷穿過天母東路86巷就可以到達東和公園，它的面積約有3.1公頃，於民國88年闢建完成，備有平面停車場、表演舞台，羽球場等設施，玉潮坑溪及其支流穿越其間，可說是純粹自然與休閒功能兼備的都會公園。每到假日就吸引許多全家大小來到公園遊憩，尤其在停車場上方斜坡草坪，享受滑草嬉戲或悠閒躺滾的天倫之樂。 　　園內植有山櫻花、阿勃勒、大花紫薇、欒樹、艷紫荊、楓香等喬木，隨著春、夏、秋、冬輪番在園內上色，使附近居民生活也跟著四季更迭有著多彩的變化。而潺潺溪流、樹木參天，於轉身間偶然也會發現藍鵲、灰樹鵲、黑冠麻鷺、松鼠等穿梭其間，像極了一個偌大的自然教室，是兼具知性與感性的優美空間。 　　更特別的是，東和公園不僅與天和公園綠意相乘，並且還以中山北路為界與天母公園，形成天母地區的二條綠帶，宛如左右肺片，讓整個區域時時充滿了氧氣，處處呈現著健康樂活的景象。",
