        "pm_overview": "　　位於台北市士林區永平街20巷底左側，自早年三樂建設在社子地區建築公寓時，將延平北路六段社子派出所後方的一塊土地闢建為三樂花園，園內假山水池、涼亭城樓俱備，且挖掘出原住民活動遺跡，民國78年時，本府將三樂花園徵收，並更名為社子公園，是社子地區最大的公園，也是一處充滿故事的休閒園地。\r\n　　面積有1公頃多的社子公園，園內水池、彩繪涼亭、兒童遊具、羽球場、涼亭、閱覽室等設施組成一個富有古意多元的社區公園，社子公園儼然成為社區居民休閒交誼的重要場所。就連園中前後種植的大王椰子、榕樹、黃椰子、豔紫荊等喬木，也些許透露出公園歷久至今的微妙變化。 　　在社子島裡的一塊饒富意味的綠洲，隨著無數日月星辰，也不禁引人想探索的意念。",
