        "pm_name": "海光公園",
        "pm_overview": "　　葫蘆國小前的海光公園，園內除了有涼亭、籃球場、兒童遊戲區、地下停車場等設施，滿足不同年齡層的生活需求外，還座落著葫蘆堵居民的信仰中心海光宮。是相傳有一農民在基隆河與淡水河交界處番仔溝，發現觀音佛祖神尊，在村民們建造小型的磚石廟宇，將觀音佛祖神尊奉祀於內，取名為「海光宮」。\r\n　　知道嗎？士林區最老的樹就在園內，樹蔭寬廣，矗立中央，不僅提供民眾生活休閒的綠色空間，也守護著公園內的一草一木。又由於西依鄰著環河北路，高架道路又貫穿其上，凡駕駛行經於此，滿眼花草綠樹，疲勞視覺頓時放鬆了許多。\r\n　　海光公園是最融入社區生活的都會公園，伴著遠近遐邇的海光宮古老傳說，又提供交通便捷的功能性，文化與文明的交錯，一種絶對的生活感。",
