        "pm_overview": "　　明水公園位於北安路538巷口，鄰永安國小，公園面積約1公頃，是一個小巧卻機能豐富的公園。走進公園內直接映入眼簾的是滿滿的綠意、挺拔的大王椰子、茂盛的水黃皮、色彩豐富的臺灣欒樹以及洋玉蘭、櫻花、楓香、鳳凰木、榕樹、茄苳、豔紫荊等十餘種喬木，配合不同的時期開花與變色落葉，充分展現四季變換之美，加上青翠宜人的美麗草皮，漫步其間彷彿在轉瞬間遠離塵囂，常見許多遊客在長椅上冥想，洗去一身疲累並紓緩各種壓力。\r\n　　公園內的設施有供學童恣意滑動的溜冰場、攀爬嬉戲的兒童遊戲場、讓社區居民來此聚會話家常的休憩涼亭、婆婆媽媽們舒緩筋骨的林下運動廣場等，還有迂迴卻不複雜的步道，讓不大的明水公園卻能藉此有柳暗花明又一村的遊憩體驗，充分提供附近居民一個舒展身心、運動健身的最佳去處。由於本公園緊鄰開放校園的永安國小，所以常見學童們呼朋引伴的在這裡玩得不亦樂乎，在學童們活潑的跑跳之餘，遊戲區舖設的彈性地墊充分確保兒童遊戲時的安全。102年因應設施老舊進行公園更新工程，整合園內體健設施，重新規劃兒童遊戲區，在園內的大榕樹下設計樹屋平台，不僅提供遮陰功能，更能與植物接近也增加趣味性。 　　\r\n        在明水公園裡有充滿知性的多樣性特色植栽及富有感性的宜人配置，讓你流連忘返，更有讓你玩得盡興的各項遊樂設施，有機會到這附近，別忘了給自己幾分鐘的時間，體驗一下明水公園的迷人之處。",
