        "pm_name": "康樂公園",
        "pm_overview": "　　林森、康樂公園位於南京東路一段，介於中山北路與新生北路間，被林森北路劃分成二個公園，其中林森公園面積約2.9公頃、康樂公園面積約1.4公頃，總計約4.3公頃，是臺北市中心商業區中大型的公園。原址在日據時代曾經是日本人公共墓地，光復後又成為安置榮民的處所，多年來形成歷史情感與視覺衝突共存的矛盾現象；直至民國86年3月，在都市更新及防災安全考量下於86年底完成地上物安置拆遷及公園之簡易綠化工程，再分別進行公園工程，各於92年10月及91年3月完工啟用。\r\n　　公園以都市紋理編織成的彩色繡錦及萬物生態生態共存的樂園為規劃主軸，保留歷史文化的跡，並融合自然生態的萬種風情，提供鄰近居民活動及自然教育的機會。林森公園設有多功能感官體驗區-如景石花園(觸覺)、花廊休憩區(味覺)、香味花園(嗅覺)、活動草坪及兒童遊戲場等。康樂公園規劃內容則有親子水霧廣場（視覺）、噴水池（聽覺）、花樹丘陵區、散步小徑、緩衝林帶、公廁等設施，是適合闔家同遊的休憩景點。",
