        "pm_overview": "　　內湖110號綠地位於內湖區瑞光路112巷及76巷旁內，面積1,318平方公尺，係由大都市、住一建設等於西元1996年開闢完成捐贈本府。綠地為一長條形狀，平坦步道兩旁有小葉欖仁、台灣灤樹喬木等。除了欣賞植物的綠意盎然，花木扶疏，不妨靜下心細細聆聽各式鳥鳴叫聲。鄰近為辦公大樓與住家於綠地內悠閒走走，在樹下悠哉享受遠離喧囂的寧靜氛圍，在台北市高樓大廈旁眼下就難得有此寧靜帶狀的步道，供里民休閒漫步其間時有放鬆舒解壓力休憩的感受自有怡然恬適之情。",
