        "pm_overview": "　　近年來全球暖化與節能減碳之環保議題持續受到重視，為減少私人運具使用與發展永續環境，臺北市目前積極推廣民眾運輸與自行車等綠色運具，並規劃及設計自行車道提供自行車族群更安全及便捷之交通環境，以帶動潛在之自行車使用者。更成為市府重要政策之一。\r\n　　自行車道工程地點位於臺北市內湖區行善里與週美里，設計範圍起自舊宗路、安興街口，沿安興綠地至潭美街，銜接彩虹橋牽引道全長約1公里。兩旁有台灣榔榆、緬梔、大王椰子、茄冬、豔紫荊等大型樹木，搭配新設立座椅可供民眾使用，該綠地最大特色是原有景觀很自然的結合市民生活機能規劃，除自行車道設施外，沒有硬梆梆的土木設施，讓市民有個很優質的休憩場所又可運動的綠地。",
