        "pm_name": "康樂綠地",
        "pm_overview": "康樂綠地位於康樂街191巷旁，面積15,445平方公尺，西元2010年先行闢建3,613平方公尺。綠地內 種植濃密的榕樹、樟樹、黑板樹、茄冬、台灣灤樹、楓香、鳳凰木、垂榕、烏心石、橄仁等十餘種喬木，休閒健身功能極為完備，除設有一圓形廣場、涼亭、組合遊具、搖搖樂、翹翹板及單桿、三人上肢牽引器體健設施等，每年都藉圓形廣場配合節慶辦理有聲有色晚會活動，帶給市民無限歡樂及一個健康休憩場所，更有附近的學童來到兒童遊樂區校外教學，進行遊戲活動。\r\n綠地內由幾條步道貫穿，行走在踏石步道上，看著一大片綠油油的草皮，聽著蟲鳴鳥叫聲不絕，享受大自然的精華提升免疫力，使身心健愉的過著人生的每一天。此綠地是提供民眾運動及休閒的好去處。吸望大家來體驗。",
