        "pm_overview": "　　位於南京東路6段沿線，由新明路298巷起至成功路二段間的週美綠地，開闢於西元1996年，全區共佔地12,844平方公尺，因交通繁忙又為處高架麥帥公路下方，狹長帶狀園區種植臺灣欒樹、鳳凰木、茄冬、榔榆、金露華、蜘蛛百合及草坪綠美化，綠樹成蔭可以隔離降低快速道路所產生的噪音，沿著步道設有多處座椅供民眾隨時可停下腳步休息，在充滿綠意的環境裡和好朋友談天說地。\r\n　　公園裡設有兒童遊戲區和體健設施，非常受到小朋友們及運動民眾的喜愛，也是建立親子互動的好地方，歡迎您常來潭美公園走走，享受一下城市難得鬧中取靜的休憩空間。",
