        "pm_overview": "　　大同16號廣場位於大同區永樂里環河北路與民生西路口，於民國84年開闢完成，廣場面積為255平方公尺，該廣場設置公共藝術「大稻埕之春」，以青銅、花崗石為雕塑材質，作品呈現母子相擁，一同眺望遠方，充滿期待幸福與美滿之意象，母親象徵勤勞儉樸，相夫教子典範，男孩則代表著希望的未來，瞻仰遠方的母子，洋溢著快樂的心情，期待美好的遠景。另外作品中以石板堤牆穩喻懷舊濃情，以水波浪紋代表特殊地理位置-九號水門，歡迎大家前往參觀。",
