        "pm_name": "中央藝文公園",
        "pm_overview": "　　「中央藝文公園」，當初係由行政院文化建設委員會，為與華山創意文化專用區整體景觀考量而規劃設計，達到串連中央車站至公園間綠帶整體一致景觀，於90年間由本府都市發展局代辦簡易綠美化工程，並於95年間開闢完成。其中公園用地範圍土地所有權單位分屬於行政院文化建設委員會、交通部臺灣鐵路管理局及本府等，市有土地公園部分本處於96年1月1日接管維護。\r\n　　本處公園位於杭州北路與北平東路口，範圍即以草坪區以北（含草坪區）、南北向中軸園路以西，面積26,700平方公尺，園區內有石椅、公廁、涼亭、體健等設施，提供民眾活動休憩及種植喬木、灌木、草皮綠美化等，尤其廣闊大草坪區種植白花苜蓿，於每年3月份時盛開，開滿小白花更是蔚為奇觀。",
