        "pm_name": "介壽公園",
        "pm_overview": "　　「介壽公園」闢建前為國防部三軍籃球場（其前身為市立第一女中球場），都市計畫公告日期為民國45年5月4日，編號為中正71號，於民國60年建造，因位處原介壽路上，故命名為「介壽公園」，並一直沿用迄今。\r\n　　「介壽公園」位於凱達格蘭大道、公園路、懷寧街及貴陽街等交界口，毗鄰二二八和平公園、景福門、外交部及市立北一女中，面對總統府位處要衢。於民國60年建造，面積13,156平方公尺，園內遍植喬、灌木，園景設施有花架、涼亭、水池、假山、園路、坐椅、並有林森銅像、白色恐怖政治受難者紀念碑等。\r\n　　園區內喬灌木生長良好，各項設施均由本處同仁細心維護，提供市民、遊客及附近機關、學校、公司人員（如總統府、外交部、國家圖書館、臺北市立教育大學、北一女中、臺北賓館）等過往人潮入園休憩及優質的活動空間，以發揮都市公園休憩、調節氣溫、防塵防避災害及美化環境功能。",
