        "pm_overview": "本公園屬老生代公園。公園面積：2453平方公尺。\r\n公園位置：基隆路1段380巷，座落中興里區內。\r\n公園沿革：於民國70年開闢，84年7月1日接收，90年完成整建。\r\n公園特色：\r\n中興公園毗鄰忠駝國宅和光復市場、國父紀念館，為大安區及信義區交界處一高品質住宅區，跨處信義區中興里與西村里間，為聯繫兩里里民間情感及提供兩里社區活動地點的重要場所。\r\n入口意象地磚，與樹林間滲出的光芒相互輝映，顯現出公園明亮朝氣感。園內概分為活動廣場、休憩區、健身區、兒童遊樂區花架涼亭休憩區，健康步道等多樣性活動場所，適合不同年齡層的人前往活動。 外圍弧形花台配置座椅塑造活潑的層次感，石板步道蜿蜒於如茵的綠草中。公園周邊人行道與道路間以複層式灌木植栽區隔，老樹緊挨著住宅大樓生長，形成一條綠線的林蔭步道與社區住宅景觀結合，使環境更顯清幽。曾獲頒91年度臺北市績優鄰里公園入選獎。",
