        "pm_overview": "本公園屬中生代公園。公園面積：1766平方公尺。\r\n公園位置：虎林街30巷與永吉路321巷邊。\r\n公園沿革：於民國76年開闢，84年7月1日接收，並於90年12月重新整建完成。\r\n公園特色：\r\n「四育公園」行政轄屬信義區四育里，原名「正義公園」，91年應里民之建議更名為「四育公園」。本公園呈現特質為陽光活力，結合兒童遊憩空間，擴大廣埸的容受力，並利用空間互動語彙，植栽與材質的特性，創造親切之社區活動空間，增加整體園區之空間韻律感，提昇居民之親園活動品質，花籬的設計隔離週邊停車雜亂景象的衝擊，東側入口意象廣埸設計，凝聚公園陽光活力及廣場整體感，提升社區公供休憩設施之服務品質，造就社區的優質生活環境。",
