        "pm_overview": "本公園屬老生代公園。公園面積：2200平方公尺。\r\n公園位置：永吉路278巷57弄內。\r\n公園沿革：於民國67年開闢，84年7月1日接收，93年社改整修完成。\r\n公園特色：\r\n永吉公園園內設有兩座花架式涼亭，小型活動廣埸與兒童遊戲空間使用者眾，週邊步道擺設休閒椅，市場過往民眾小憩閒話家常，由於公園歷史悠久不乏林蔭，涼亭邊更滿是綠意。93年改建完工，去除舊有高牆阻隔缺點，並增加多元功能性，轉換新氣象營造更符合民眾所需的新優質環境。藉由改造完後之新氣象加上楊吳里長秀好及全體義工的用心妝點，於同(93)年參加臺北市花卉展鄰里公園評選，獲頒優選獎，整體環境在義工們細心照料下園區景觀時時保持欣欣象榮之景象。",
