        "pm_overview": "　　建造於民國83年，屬狹長型綠地佔地11807平方公尺，其中有自行車道也有YouBike租借站可以供民眾代步。\r\n　　自行車道兩側主要種植茄冬及印度紫檀，人行步道則是水黃皮及楓香。附近鄰近新光三越、101及信義誠品，松智公園處也有設置座椅供民眾稍作休憩。",
