        "pm_transit": "公車:搭乘22公車至世貿中心於莊敬路下車\r\n搭乘207公車至世貿中心下車 \r\n搭乘284公車至世貿中心下車 \r\n搭乘33公車至世貿中心於莊敬路下車\r\n捷運:市政府站，步行至松壽路約10分鐘由市府站搭乘537公車至世貿中心,於莊敬路下車\r\n可從捷運站台北車站、大安站、中正紀念堂 或 東門站 轉乘到捷運信義線搭至【台北101世貿站】，【五號出口】或【一號出口】出站即是世貿一館(約步行2分鐘即可到達)\r\n可從捷運站站台北車站、忠孝新生、忠孝復興或站南港展覽館轉乘到站捷運板南線搭至站【市府站】下車後，可轉搭站537公車於市府路台北世界貿易中心展覽大樓下車，或由2號出口沿基隆路南行至1-6號道路或松高路左轉，右轉市府路南行即可抵達世貿一館。",
