        "pm_name": "虎林公園",
        "pm_overview": "本公園屬中生代公園。公園面積：9547平方公尺。\r\n公園位置：信義路6段76巷8弄與松德路307巷。\r\n公園沿革：於民國76年開闢，84年7月1日接收，92年社改整修完成。\r\n公園特色：\r\n虎林公園位於四獸山象山山麓下，因高差影響，週邊無穿越性交通道路，公園呈完整封閉之環境，南側鄰市立療養院，故穿越公園之松德路搭車之民眾不在少數。公園內有一座標準之籃球場，還設有兒童遊戲場，多功能組合遊具、翹翹板及單槓提供小朋友玩耍，原有溜冰場因使用率不高，已改善成為多功能之廣場，可供社區舉辦活動之場所。公園有一大特色為綠地面積很大，週邊大樹下設有涼亭、石板椅是民眾遮陽休憩好地方。更獲得99、96、91年度臺北市績優鄰里公園入選獎。",
