        "pm_name": "春光公園",
        "pm_overview": "本公園屬中生代公園。公園面積：4241平方公尺。\r\n公園位置：忠孝東路5段、林口街口。\r\n公園沿革：於民國75年開闢，84年7月1日接收，93年停管處整建完成。\r\n公園特色：\r\n由於地下停車場興建，新建公園於93年3月重新啟用。位處於忠孝東路5段、中坡北路及大道路口之交通要衝，公園中心設有一座魔術方塊造形之公共藝術，成為公園主要焦點。整體規劃空間利用性高，動線順暢植栽多樣，配置分明顏色鮮豔；喬木分佈於外圍種植以信義區區樹青剛櫟為主，廣場週邊灌木則以月橘、細葉雪茄、信義區區花野牡丹等作層次舖陳，廣場部分間植有綠油油的類地毯草，柔化生硬的鋪面，兒童遊戲區寬敞且設備完善。是熱鬧的忠孝東路旁一處休憩的好地方。",
