        "pm_name": "革新公園",
        "pm_overview": "本公園屬中生代公園。公園面積：1585平方公尺。\r\n公園位置：松隆路19-33旁。\r\n公園沿革：於民國76年開闢，84年7月1日接收。\r\n公園特色：\r\n革新公園整潔寬敞的人行步道環繞公園四周，園內設有兒童遊戲場、卵石健康步道、涼亭等設施，兒童遊具色彩鮮豔，造型可愛，吸引附近小孩來園玩耍嬉戲；園內植栽豐富，周圍的黑板樹及小葉欖仁生長茂密，喬木下的仙丹嬌豔奪目、花圃內黃金露花、變葉木、四季海棠、玫瑰等層次分明，將公園妝點成一美麗的社區公園，提供民眾一個綠意盎然的休憩空間。曾獲頒89年度臺北市績優鄰里公園入選獎。99年度臺北市績優鄰里公園佳選獎。",
