        "pm_name": "泰和公園",
        "pm_overview": "本公園屬老生代公園。公園面積：1422平方公尺。\r\n公園位置：吳興街583巷與600巷67弄間。\r\n公園沿革：於民國69年開闢，84年7月1日接收。\r\n公園特色：\r\n公園略呈三角形，由於緊鄰住宅區，平日使用即與社區民眾生活息息相關，舉凡元極舞、韻律操、老人棋弈、泡茶等日常活動或地方民俗酬神廟會均藉由公園進行著，充分發揮公共空間之使用功能。\r\n泰和公園外環之人行步道，經於89年運用福德坑回饋經費予以整修而煥然一新，公園中央廣場區是民眾主要的活動區域，尤其清晨及晚間，承載著數不清的足跡；入口處兒童遊戲區內的溜滑梯、搖搖樂、單槓等設施，提供小朋友嬉戲玩樂的空間，公園東側設有休憩涼亭、健康步道，在茂密的榕樹、水黃皮、榔榆等喬木覆蓋下，構成涼爽宜人的林蔭帶。",
