        "pm_name": "富台公園",
        "pm_overview": "    富台公園位於信義區松信路右側與臺北兒童福利中心間，周圍商業區、住宅區及私人機關林立，於民國69年開闢，並於103年全面更新，公園面積1,959平方公尺，位居信義計畫區周邊地帶。公園西向入口設有小型花圃及大理石雕公園名牌，中央設置活動廣場，周邊設有體健區、兒童遊樂區、花架、涼亭及座椅，滿足不同年齡層民眾平常休憩運動需求，該公園可謂麻雀雖小五臟俱全!\r\n    公園內種有山櫻花、羅比親王海棗、樟樹、榕樹及黃槿等喬木，並栽植光蠟樹、春不老、野牡丹、紫藤、仙丹花等植物綠美化。漫步園中，有鳥叫、蟬鳴伴隨，在綠蔭扶疏下，試著在繁鬧的臺北市中呼吸著清新的氣息，用心連結與臺北市的情感。",
