        "pm_name": "福祥公園",
        "pm_overview": "本公園屬中生代公園。公園面積：835平方公尺。\r\n公園位置：永吉路127巷與松隆路99號邊。\r\n公園沿革：於民國76年開闢，84年7月1日接收。\r\n公園特色：\r\n為一小巧玲瓏的三角公園。公園位處道路交叉路口，亦為橫跨松隆路人行陸橋之起點，為繁忙的道路交界區提供綠色空間，也是社區居民及路人得以駐足休憩的中途站。\r\n公園入口處的地標景石，搭配色彩繽紛及層次分明的白鶴芋，朱槿、變葉木、黃金露花等植栽，成為整座公園視覺的焦點。\r\n本公園屬中生代公園。公園面積：835平方公尺。 公園位置：永吉路127巷與松隆路99號邊。 公園沿革：於民國76年開闢，84年7月1日接收。 公園特色： 為一小巧玲瓏的三角公園。公園位處道路交叉路口，亦為橫跨松隆路人行陸橋之起點，為繁忙的道路交界區提供綠色空間，也是社區居民及路人得以駐足休憩的中途站。 公園入口處的地標景石，搭配色彩繽紛及層次分明的白鶴芋，朱槿、變葉木、黃金露花等植栽，成為整座公園視覺的焦點。 福祥公園以一白色涼亭為中心，周邊植有樹蔭茂密的榕樹、印度黃檀、阿勃勒等喬木，與環繞公園周邊的步道，構成舒適宜人的林蔭休閒帶。涼亭前的兒童遊戲區設有溜滑梯、攀爬架及造型可愛之搖搖樂小木馬，為顧及幼兒嬉戲安全，舖有彈性地墊，同時也設置座椅提供家長就近照顧及閒話家常，是適合親子共處的社區公園。",
