        "pm_overview": "本公園屬老生代公園。公園面積：1014平方公尺。\r\n公園位置：和平東路3段435巷7弄。\r\n公園沿革：於民國66年開闢，84年7月1日接收，91年社改整修完成。\r\n公園特色：\r\n黎忠公園面對捷運木柵線麟光站，民眾一般日常上班之通勤要徑。曾於91年進行公園景觀改造，園內具有多功能休閒設施：有提供社區活動場地的活動廣場、孩童玩耍的遊戲區、溜冰場、設於樹蔭下的健康步道等。可供社區居民休憩的花架廊道蜿延於植栽遍佈園區內，活動區與景觀休憩區明顯區隔互不干擾充份達到空間利用功能性。兒童遊戲區以木平台建構搭配組合式遊樂設施增加兒童之使用率，週邊綴以活潑可愛草花使廣場動感十足。提供市民在繁忙緊張的工作後一個良好活動、休閒的好場所。",
