        "pm_overview": "　　於民國79年由震旦行捐贈原公園旁的部分土地合併而成，正式改名為「景聯廣場」。位於吳興街7巷與莊敬路6巷之間，佔地2,055平方公尺。廣場內種植黑板樹、阿勃勒、小葉欖仁、麵包樹、楓香、饅頭樹、桑樹、茄冬、朱蕉、七里香、木麻黃、黛葉粉、杜鵑、仙丹花、鵝掌藤等多種喬、灌木。廣場除設有一圓形廣場外另一旁則設有體健設施、兒童遊戲區及座椅，供民眾隨時可停下腳步休息，其中遊戲區及體健設施更是受到小朋友及運動民眾的喜愛。",
