        "pm_name": "百福公園",
        "pm_overview": "    百福公園位於南港區福德街309巷19號對面，於民國73年闢建，公園處94年接管，公園面積2,756平方公尺，走進公園即可被常綠灌木六月雪所拼湊出的「百福」二字所吸引，公園名稱便不明而喻。\r\n園內有1株文化局列管的受保護樹木-錫蘭橄欖，錫蘭橄欖因核果形狀和橄欖很像，又產於錫蘭，故得此名。果實外形很像橄欖，但不是真正的橄欖，其果肉薄而味酸，可加工醃漬成蜜餞。\r\n公園內有兒童遊具、涼亭及溜冰場等設施，栽植櫻花、酒瓶椰子、大王椰子、龍柏等喬木，搭配番茉莉、巴西野牡丹、宮粉仙丹、茶梅等開花性灌木，使公園色彩更佳豐富。",
