        "pm_overview": "    萬福公園位於忠孝東路6段70巷內，面積1954平方公尺，於民國73年開闢屬社區型的鄰里公園，本公園面積雖小但設施卻很多元，具有涼亭、兒童遊戲場、小型表演場及體健設施等多樣性的空間，為當地居民活動聚會的好場所。\r\n    公園內種植有刺桐、鳳凰木、台灣欒樹、小葉欖仁及櫻花等喬木，部分喬木下方種植耐陰性強的白鶴芋及鵝掌藤等觀葉植物，利用高矮不同之植栽配置，呈現複層式效果。園路周邊種植有仙丹、桂花、樹蘭、杜鵑、野牡丹等開花性灌木及入口節點搭配植栽設計，開花時節，色彩奪目艷麗，增添公園美景，隨著四季變化有不同的綠化景觀，是提供市民紓解身心的小空間。",
