        "pm_overview": "    小坑一號公園位於文山區萬壽路62之1號旁，開闢面積為3,800平方公尺，公園坡度大於30度，高地落差較大，故園內多植草皮護坡，並設有步道，提供市民休憩使用。\r\n　　公園內種植山櫻花200餘株，早春時節櫻花遍地綻放，吸引遊客駐足，園路旁另設有座椅，可供賞花民眾駐足觀賞櫻花林美景。小坑一號公園地勢較高，天氣晴朗時刻，可從萬壽路遠眺台北101大樓，亦能將都市風景盡收眼底，美不勝收。\r\n",
