        "pm_overview": "    文山（景美）49號公園位於文山區景華街128巷3弄，於民國101年部分開闢，開闢面積為340平方公尺，旁有景興國小鄰近住宅，公園依山坡地形闢建，設有枕木步道環繞公園，可供民眾親近原始林木，享受大自然的芬多精。\r\n　　公園內設有木造涼亭，供市民休憩使用，也成為附近居民閒話家常的好所在，園內種植野薑花、巴西野牡丹、變葉木、羅漢松、山櫻花等，植栽多樣且色彩豐富，不僅增添公園的美，也成為附近小學生們的活教材。\r\n",
