        "pm_overview": "　　萬芳11號公園位於文山區萬美街一段與萬寧街交叉口，於民國74年開闢，開闢面積為10,906平方公尺。公園依山坡地形開闢，坡地部分保留原有林相，園區內並規劃登山步道可由萬美街通往萬美社區國宅。\r\n　　公園綠地種植榕樹、山櫻花、阿勃勒、大花紫葳、豔紫荊及海檬果等喬木，並栽植小葉赤楠、桂花、扶桑及馬茶花等灌木加強綠美化。園內設施有兒童遊戲場、體健設施、多功能活動廣場及中式休憩涼亭，提供市民遊玩及運動休憩使用，園內設置中式圓形拱門並結合矮牆加強入口意象，讓遊客享有古色古香別有洞天及清幽園林的氛圍，公園內登山步道旁山溝於夏季時段有螢火蟲飛舞蹤跡，是鮮為人知的秘密景點，期待您來此尋幽探螢。",
